.statistics {
  margin: 0 auto;
  width: 450px;
  background-color: rgb(148, 216, 255);
  margin-bottom: 20px;
}
.statList {
  padding: 0;
  margin: 0 auto;
  list-style: none;
  display: flex;
  width: 100%;
  background-color: #fff;
}
.title {
  margin: 0;
  text-align: center;
}
.item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 50px;
  width: 100px;
  border: solid 1px gray;
}
